// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from '../App'
import moment from 'moment'

import Router from 'vue-router'
import HelloWorld from '@/components/HelloWorld'
import 'html5-reset/assets/css/reset.css'



// 主页
import applyForTest from '@/views/apply-for-test' //申请众测
import verifyAddress from '@/views/verify-address' // 确认收货地址

import successList from '@/views/apply-success-list' //查看申请成功列表
import verifyApplyInfo from '@/views/verify-apply-info' //查看申请信息
import mytestdetail from '@/views/my-test-detail' //我的众测详情页(列表)
import hotTestProductList from '@/views/test-product' // 众测产品
import hotTestExperience from '@/views/test-experience' // 众测体验报告

import testProductDetail from '@/views/test-product-detail' //众测产品详情页
import testExperienceReportDetail from '@/views/test-experience-report-detail' //众测产品详情页


Vue.use(Router)
Vue.filter('dateFormat', function (value, type) { //时间格式化过滤
  return moment(value).format(type);
});

const router = new Router({
  // mode:'history',
  routes: [{
      path: '/',
      name: 'HelloWorld',
      component: HelloWorld
    },
    {
      path: '/applyForTest',
      name: 'applyForTest',
      component: applyForTest
    }, {
      path: '/successList',
      name: 'successList',
      component: successList
    },
    {
      path: '/verifyApplyInfo',
      name: 'verifyApplyInfo',
      component: verifyApplyInfo
    },
    {
      path: '/mytestdetail',
      name: 'mytestdetail',
      component: mytestdetail
    },
    {
      path: '/hotTestProductList',
      name: 'hotTestProductList',
      component: hotTestProductList
    },
    {
      path: '/hotTestExperience',
      name: 'hotTestExperience',
      component: hotTestExperience
    },
    {
      path: '/verifyAddress',
      name: 'verifyAddress',
      component: verifyAddress
    },
    {
      path: '/testProductDetail',
      name: 'testProductDetail',
      component: testProductDetail
    },
    {
      path: '/testExperienceReportDetail',
      name: 'testExperienceReportDetail',
      component: testExperienceReportDetail
    },

  ]
})


Vue.config.productionTip = false

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  components: {
    App
  },
  template: '<App/>'
})



